.container{padding:12px}.container :global .ant-card-head{font-weight:400;height:64px}.container :global .ant-card-head-title{padding:20px 0}.container :global .ant-card-body{position:relative}.title{display:flex;justify-content:space-between}.address{cursor:pointer;padding:4px 12px;border-radius:4px;transition:all .1s cubic-bezier(.645,.045,.355,1)}.address:hover{background:#f5f5f5}.icon{color:#98a9bc}.account{display:flex}@media (max-width:575px){.account{flex-direction:column}}.avatar{height:64px;width:64px;border:3px solid #e8ecef;padding:4px;background:#fff}.avatar img{border-radius:50%}.user{display:flex;flex-direction:column;align-items:center}.view{background:#f2f4f6;color:#778ca2;font-size:14px;border:none;cursor:pointer;height:32px;width:64px;line-height:32px;margin-right:0;text-align:center}.operation{display:flex;flex-direction:column;align-items:center;position:absolute;right:32px}@media (max-width:575px){.operation{margin-top:16px;position:unset;right:0}}.money{text-align:center;margin-bottom:36px;letter-spacing:.3px;max-width:240px;height:96px}@media (max-width:575px){.money{margin-bottom:12px}}.reload{margin-left:8px;color:#98a9bc;cursor:pointer;transition:all .3s cubic-bezier(.645,.045,.355,1)}.reload:focus,.reload:hover{color:rgba(152,169,188,.65)}.balance{font-size:24px;font-weight:500;letter-spacing:0;margin-top:8px}.balance :global .ant-statistic-content{color:#a90433}.balance :global .ant-statistic-title{color:#252631;font-weight:400}.balance :global .ant-statistic-content-suffix{font-size:inherit}.button{display:flex;margin:4px;justify-content:center;width:100%}.send{height:48px;width:120px}.receive{height:48px;width:120px;margin-right:8px;background:#98a9bc;border-color:#98a9bc}.receive:active,.receive:focus,.receive:hover{background:rgba(152,169,188,.65);border-color:rgba(152,169,188,.65)}